(function() {
  'use strict';

  angular
      .module('fisioCamallera')
      .service('news', news);

  /** @ngInject */
  function news($http, $q) {
    /*var newsData = [
      {
        'title': 'Des del Fisiocam et desitjo',
        'image': 'news/nadal2015.jpg',
		'description': 'Des del Fisiocam et desitjo',
        'url': '',
		'date': '25/12/2015',
        'text': ['Agraeixo la teva confiança que t\'ha portat al meu centre de fisioteràpia i alli em trobaràs també a l\'any vinent, sempre que em necessitis.',
                 'I si vols millorar la salut d\'una altra persona, hi tinc preparats',
                 '<strong>Vals de regal</strong>','','Una abraçada','Martina Narloch']
      }
    ];*/

    this.getNews = getNews;
	this.getNewsBy = getNewsBy;

    function getNews() {
		var defered = $q.defer();
        var promise = defered.promise;
		
		$http.get('assets/news/news.json').success(function(data) {
			defered.resolve(data);
		}).error(function(err) {
			defered.reject(err)
        });
		
		return promise;
    }
	
    function getNewsBy(newsId) {
		var defered = $q.defer();
        var promise = defered.promise;
		
		$http.get('assets/news/news.json').success(function(data) {	
			for(var i = 0; i<data.length;i++) {
				if (data[i].id == newsId) {
					defered.resolve(data[i]);
					break;
				}
			}
		}).error(function(err) {
			defered.reject(err)
        });
		
		return promise;
    }	
	
  }

})();

(function() {
  'use strict';

  angular
    .module('fisioCamallera')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          relativeDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment, $log, ngTranslation, $scope, news) {

        // "vm.creation" is avaible by directive option "bindToController: true"
        //vm.relativeDate = moment(vm.creationDate).fromNow();
        
		//$scope.language = ngTranslation.getLang
		// obtain language list from servide
		
		$scope.newsCount = 0;
		
		activate();
		
		function activate() {
			getNewsThings();
		}
		
        $scope.changeLanguage = function (lang) {
            $log.debug('NavbarController.changeLanguage to '+lang);
			$scope.language = lang;
            ngTranslation.use(lang);
        };
		
		function getNewsThings() {
			news.getNews().then(function(data) {
				$log.debug(data);
				$scope.newsCount = data.length;
			})
			.catch(function(err) {
				$log.error(err);
			});
		}
		
    }
  }

})();

